import {useSpring, animated} from 'react-spring'
import * as easings from 'd3-ease'

const Menu = ({fontSize, navOpen, nextPage}) => {

    const LinkAnimate = (index) => useSpring({
        from: {
            marginLeft: -10,
            opacity: 0
        },
        to: {
            marginLeft: !navOpen?0:-10,
            opacity: !navOpen?0:1
        },
        delay: 1200 + (index* 150),
        config: {
            duration: 450,
            easing: easings.easeCubic
        }
    })

    return (
        <animated.div className={`menu ${navOpen?'menu-active':''}`} style={Menu}>
            <div className="menu-links">
                <animated.h1 style={LinkAnimate(1)} onClick={() => nextPage("Works")}>Work</animated.h1>
                <animated.h1 style={LinkAnimate(2)} onClick={() => nextPage("Projects")}>Projects</animated.h1>
                <animated.h1 style={LinkAnimate(3)}>About</animated.h1>
                <animated.h1 style={LinkAnimate(4)}>Contact</animated.h1>
            </div>
            <div className="menu-buttons">
                <button>Message</button>
                <button>Mail</button>
            </div>
        </animated.div>
    )
}

export default Menu