import React, { useState, useEffect } from 'react'
import HomeProjects from './homeProjects'
import {useSpring, animated} from 'react-spring'
import SplitText from './splitText'
import * as easings from 'd3-ease'

const HeroSection = ({fontSize, setPage, page}) => {

    const header = "I'm Prateek, a \nUI/UX Designer \nand a \nFull-Stack Web Developer\n. Striving to create amazing experiences\n, on web and mobile devices.".split("\n")

    const Shift = useSpring({
        to: {
            marginTop: 0
        }, 
        from: {
            marginTop: window.innerHeight/4
        }, 
        delay: 7800,
        config: {
            duration: 600,
            easing: easings.easeCubic
        }
    })

    const [headerClass, setHeaderClass] = useState("")

    

    useEffect(() => {
        const headerChange = () => {
            if(page !== "Home"){
                setTimeout(() => {
                    setHeaderClass("overflow-none")
                }, 1200)
            } else {
                setHeaderClass("")
            }
        }
        headerChange()
    }, [page])

    return [
        <div className={`hero ${headerClass}`}>
            <animated.h1 className="hero-header" style={Shift}>
                <SplitText bold={["UI/UX Designer ", "Full-Stack Web Developer"]}>
                    {header}
                </SplitText>
            </animated.h1>
        </div>,
        <HomeProjects fontSize={fontSize} setPage={setPage} page={page} headerClass={headerClass}/>
    ]
}

export default HeroSection