import {useState, useEffect, useRef, lazy, Suspense} from 'react'
import NavBar from './components/nav'
import Loader from './components/loader'
import HeroSection from './components/hero'
import Menu from './components/menu'
const Item = lazy(() => import('./components/details'))
const WorkSection = lazy(() =>  import('./components/work'))
const ProjectSection = lazy(() =>  import('./components/projects'))

const App = () => {

  const Projects = ["Dummy One", "Dummy Two", "Dummy Three", "Dummy Four"]  
  const Works = ["Dummy Work One", "Dummy Work Two", "Dummy Work Three", "Dummy Work Four"]


  const pageRef= useRef(null)

  const [fontSize, setFontSize] = useState(0)
  const [burgerOpen, setBurgerState] = useState(false)
  const html = document.querySelector('html')

  const [page, setPage] = useState("Item")
  const [pageHistory, setPageHistory] = useState([])

  const listenBurger = () => {
    if(burgerOpen){
      html.classList.add('no-scroll')
    } else {
      html.classList.remove('no-scroll')
    }
  }

  useEffect(() => {
    console.log(page)
  }, [page])

  const nextPage = (pageName) => {
    var temp = pageHistory
    temp.push(page)
    setPageHistory(temp)
    setPage(pageName)
  }

  const prevPage = () => {
    var temp = pageHistory
    var pageName = temp.pop()
    setPageHistory(temp)
    setPage(pageName)
  }

  useEffect(() => {
    const font = window.getComputedStyle(document.querySelector('html')).getPropertyValue('font-size')
    setFontSize(parseInt(font, 10))
    console.log(`FONT SIZE: ${fontSize}`)
    console.log(pageRef)
  }, [])

  useEffect(() => {
    listenBurger()
  }, [burgerOpen])

  const navToggleHandler = () => {
    setBurgerState(!burgerOpen)
  }

  return [
    <Loader/>,
    <NavBar navOpen={burgerOpen} setNavOpen={navToggleHandler} page={page} nextPage={nextPage}/>,
    <Menu fontSize={fontSize} navOpen={burgerOpen} setPage={setPage} nextPage={nextPage}/>,
    <HeroSection fontSize={fontSize} setPage={setPage} page={page}/>,
    <Suspense  fallback={<div></div>}>
      <WorkSection fontSize={fontSize} page={page} prevPage={prevPage} Works={Works} nextPage={nextPage}/>
      <ProjectSection fontSize={fontSize} page={page} prevPage={prevPage} Projects={Projects} nextPage={nextPage}/>
    </Suspense>,
    <Suspense fallback={<div></div>}>
      <Item itemType={"Projects"} itemIndex={0} fontSize={fontSize} page={page} setPrev={prevPage}/>
    </Suspense>
  ];
}

export default App;
