import {useEffect} from 'react'


const Loader = () => {

    const loader = document.querySelector('.loader')
    const loaderHeader = document.querySelector('.loader h1')
    const renderElem = document.querySelector("#root")

    useEffect(() => {
        document.addEventListener('readystatechange', (event) => {
            if(event.target.readyState === 'complete'){
                loaderHeader.classList.remove('blink')
                loaderHeader.classList.add('hide')
                setInterval(() => {
                    loaderHeader.innerHTML = "Hello."
                    setTimeout(() => {
                        loaderHeader.classList.remove('hide')
                    }, 300)
                }, 300)
                setTimeout(() => {
                    renderElem.classList.remove('loading')
                    loader.classList.add('hide')
                }, 1600)
            }
            if(event.target.readyState === 'loading' || event.target.readyState === 'interactive'){
                loader.current.style.opacity = 1
            }
        })
    }, [])

    return null
}

export default Loader