import React from 'react'
import {useSpring, animated} from 'react-spring'
import * as easings from 'd3-ease'

const SplitText = ({ children, bold }) => {
    const Animation = i => 
        useSpring({ 
          to: {
            opacity: 1, 
            marginLeft: 0
          }, 
          from: { 
            opacity: 0, 
            marginLeft: -20 
          }, 
          delay: (i * 750) + 3600,
          config: {
            mass: 2,
            easing: easings.easeCubic
          }
        })
    return children.map((item, index) => (
      <animated.span key={index} style={Animation(index)} className={`${bold.includes(item)?'header-bold':''}`}>
        {item}
      </animated.span>
    ))
  }

export default SplitText