import React from 'react'
import TextTransition, {presets} from 'react-text-transition'
import {useSpring, animated} from 'react-spring'
import * as easings from 'd3-ease'

const NavBar = ({navOpen, setNavOpen, nextPage}) => {
    
    const Appear = useSpring({
        opacity: 1,
        from: {
            opacity: 0
        },
        delay: 9400,
        config: {
            duration: 600,
            easing: easings.easeCubic
        }
    })

    return (
        <animated.nav style={Appear} className={navOpen?'nav-active':''}>
            <h1>Prateek Bose</h1>
            <div className="nav-links">
                <div>
                    <h1 onClick={() => nextPage("Projects")}>Projects</h1>
                    <h1 onClick={() => nextPage("Works")}>Work</h1>
                </div>
                <h1 className="nav-link-contact">Contact</h1>
            </div>
            <div className={`burger ${(navOpen)?'burger-active':''}`}>
                <h2>
                    <TextTransition
                        text={ `${(!navOpen)?"Menu":"Close"}` }
                        springConfig={ presets.default }
                        delay = {600}
                    />
                </h2>
                <div className="burger-icon" onClick={setNavOpen}>
                    <div className="burger-line line1"></div>
                    <div className="burger-line line2"></div>
                    <div className="burger-line line3"></div>
                </div>
            </div>
        </animated.nav>
    )
}

export default NavBar